@import url("https://fonts.googleapis.com/css2?family=Barlow:wght@600&family=Fraunces:wght@700;900&display=swap");
/** Media Queries **/
.header {
  position: relative;
}
@media (min-width: 992px) {
  .header__logo img {
    width: 17rem;
  }
}
.header__nav {
  padding: 3.5rem 0;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  top: 0%;
  left: 50%;
  width: 90%;
  transform: translateX(-50%);
  z-index: 10;
}
.header__button {
  height: 2.4rem;
  position: relative;
  width: 3.3rem;
  padding: 0;
}
@media (min-width: 768px) {
  .header__button {
    display: none;
  }
}
.header__button * {
  background: #fff;
  border-radius: 1rem;
  height: 4px;
  left: 0rem;
  position: absolute;
  transition: all 0.3s ease 0s;
  transform-origin: center center;
  width: 100%;
}
.header__button span:nth-child(1) {
  top: 0rem;
}
.header__button span:nth-child(2) {
  top: calc(50% - 2px);
}
.header__button span:nth-child(3) {
  bottom: 0.01rem;
}
.header__button.activo {
  height: 2.4rem;
  position: relative;
  width: 3.3rem;
  padding: 0;
}
.header__button.activo * {
  background: #fff;
  border-radius: 1rem;
  height: 4px;
  left: 0rem;
  position: absolute;
  transition: all 0.3s ease 0s;
  transform-origin: center center;
  width: 100%;
}
.header__button.activo span:nth-child(1) {
  transform: rotate(45deg) translate(7px, 7px);
}
.header__button.activo span:nth-child(2) {
  opacity: 0;
}
.header__button.activo span:nth-child(3) {
  transform: rotate(-45deg) translate(7px, -7px);
}
.header__overlay {
  opacity: 0;
  display: none;
  transition: opacity 0.3s;
}
.header__overlay.activo {
  opacity: 1;
  background: linear-gradient(transparent, hsl(212deg, 27%, 19%));
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: block;
}
@media (min-width: 768px) {
  .header__overlay.activo {
    display: none;
    opacity: 0;
  }
}
.header__image-m {
  width: 100%;
  height: auto;
}
@media (min-width: 768px) {
  .header__image-m {
    display: none;
  }
}
.header__image-t {
  display: none;
}
@media (min-width: 768px) {
  .header__image-t {
    display: block;
    width: 100%;
    height: auto;
  }
}
.header__heading {
  text-align: center;
  position: absolute;
  top: 14.9rem;
  left: 50%;
  transform: translateX(-50%);
  width: 90%;
}
@media (min-width: 1200px) {
  .header__heading {
    top: 17.5rem;
  }
}
.header__arrow {
  background-image: url(../img/icon-arrow-down.svg);
  background-position: center;
  background-repeat: no-repeat;
  position: absolute;
  top: 29.1rem;
  left: 50%;
  transform: translateX(-50%);
  width: 4rem;
  height: 12rem;
  animation-name: updown;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}
@media (min-width: 1200px) {
  .header__arrow {
    top: 36.1rem;
  }
}

.nav {
  padding: 5.5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
  position: fixed;
  top: 10.5rem;
  left: 0;
  background-color: #fff;
  width: 100%;
  transform: translateX(-110%);
  transition-property: transform;
  transition-duration: 0.3s;
}
.nav[data-visible=true] {
  transform: translateX(0%);
}
@media (min-width: 768px) {
  .nav {
    transform: translateX(0%);
    position: initial;
    flex-direction: row;
    padding: 0;
    background-color: transparent;
    width: auto;
  }
}
.nav::before {
  content: "";
  background-color: #fff;
  width: 3rem;
  height: 3rem;
  position: absolute;
  top: -2.9rem;
  right: 0;
  clip-path: polygon(0 0, 100% 100%, 0 100%);
  transform: rotate(-90deg);
}
@media (min-width: 768px) {
  .nav::before {
    display: none;
  }
}
.nav__enlace {
  color: hsl(210deg, 4%, 67%);
  position: relative;
}
.nav__enlace:last-of-type {
  background-color: hsl(51deg, 100%, 49%);
  font-size: 1.6rem;
  padding: 1.5rem 2.5rem;
  font-weight: 700;
  text-align: center;
  color: hsl(212deg, 27%, 19%);
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  font-family: "Fraunces", serif;
  font-weight: 700;
  border-radius: 3rem;
  text-transform: uppercase;
}
.nav__enlace:last-of-type:hover {
  cursor: pointer;
  background-color: hsl(199deg, 100%, 75%);
  color: hsl(51deg, 100%, 49%);
}
.nav__enlace:last-of-type:hover {
  background-color: #ffe761;
  color: hsl(212deg, 27%, 19%);
}
@media (min-width: 768px) {
  .nav__enlace:last-of-type {
    background-color: #fff;
    font-size: 1.6rem;
    padding: 1.5rem 2.5rem;
    font-weight: 700;
    text-align: center;
    color: hsl(212deg, 27%, 19%);
    text-decoration: none;
    transition: background-color 0.3s ease;
    border: none;
    font-family: "Fraunces", serif;
    font-weight: 700;
    border-radius: 3rem;
    text-transform: uppercase;
  }
  .nav__enlace:last-of-type:hover {
    cursor: pointer;
    background-color: hsl(199deg, 100%, 75%);
    color: #fff;
  }
}
.nav__enlace:not(:last-of-type)::before {
  content: "";
  width: 100%;
  height: 0.4rem;
  background-color: #000;
  position: absolute;
  border-radius: 1rem;
  bottom: 0.2rem;
  left: 0;
  transform-origin: left;
  transform: scaleX(0);
  transition-property: transform;
  transition-duration: 0.5s;
  z-index: -1;
}
@media (min-width: 768px) {
  .nav__enlace:not(:last-of-type)::before {
    display: none;
  }
}
.nav__enlace:hover::before {
  transform: scaleX(1);
}
@media (min-width: 768px) {
  .nav__enlace {
    color: #fff;
  }
}

.features__contenido {
  display: flex;
  flex-direction: column-reverse;
}
@media (min-width: 768px) {
  .features__contenido {
    display: grid;
    gap: 0rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.features__contenido:nth-child(2) .features__learn {
  font-family: "Fraunces", serif;
  font-weight: 900;
  color: hsl(212deg, 27%, 19%);
  margin-top: 2rem;
  position: relative;
  display: inline-block;
  text-transform: uppercase;
}
@media (min-width: 1200px) {
  .features__contenido:nth-child(2) .features__learn {
    margin-bottom: 3rem;
  }
}
.features__contenido:nth-child(2) .features__learn::before {
  content: "";
  width: 115%;
  height: 1rem;
  background-color: white;
  position: absolute;
  bottom: -0.2rem;
  left: 50%;
  border-radius: 1rem;
  z-index: -1;
  transform: translateX(-50%);
  transition: all 0.2s;
}
.features__contenido:nth-child(2) .features__learn:hover::before {
  background-color: hsl(7deg, 99%, 70%);
}
.features__contenido:nth-child(2) .features__learn::before {
  background-color: #ffd2cc;
}
@media (min-width: 768px) {
  .features__contenido:nth-child(1) .features__imagen-c {
    grid-row: 1/2;
    grid-column: 2/3;
  }
}
@media (min-width: 768px) {
  .features__contenido:nth-child(2) .features__imagen-c {
    grid-column: 1/2;
    grid-row: 1/2;
  }
}
@media (min-width: 768px) {
  .features__contenido:nth-child(2) .features__info {
    grid-column: 2/3;
  }
}
.features__info {
  padding: 5.5rem 0;
  width: 90%;
  margin: 0 auto;
  text-align: center;
}
@media (min-width: 576px) {
  .features__info {
    padding: 8rem 0;
  }
}
@media (min-width: 768px) {
  .features__info {
    padding: 0;
    text-align: left;
    max-width: 30rem;
  }
}
@media (min-width: 992px) {
  .features__info {
    max-width: 35rem;
  }
}
@media (min-width: 1200px) {
  .features__info {
    max-width: 45rem;
  }
}
.features__learn {
  font-family: "Fraunces", serif;
  font-weight: 900;
  color: hsl(212deg, 27%, 19%);
  margin-top: 2rem;
  position: relative;
  display: inline-block;
  text-transform: uppercase;
}
@media (min-width: 1200px) {
  .features__learn {
    margin-bottom: 3rem;
  }
}
.features__learn::before {
  content: "";
  width: 115%;
  height: 1rem;
  background-color: #fff3ad;
  position: absolute;
  bottom: -0.2rem;
  left: 50%;
  border-radius: 1rem;
  z-index: -1;
  transform: translateX(-50%);
  transition: all 0.2s;
}
.features__learn:hover::before {
  background-color: hsl(51deg, 100%, 49%);
}
@media (min-width: 768px) {
  .features__picture-m {
    display: none;
  }
}
.features__imagen-m {
  width: 100%;
  height: auto;
}
@media (min-width: 768px) {
