.footer {
  padding: 7rem 0;
  background-color: hsl(168deg, 57%, 72%);
}
.footer__contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer__logo {
  margin-bottom: 4.5rem;
}
@media (min-width: 992px) {
  .footer__logo img {
    width: 17rem;
  }
}
.footer__nav {
  display: flex;
  align-items: center;
  gap: 4rem;
