html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Barlow", sans-serif;
  font-size: 1.8rem;
  color: hsl(232deg, 10%, 55%);
  overflow-x: hidden;
}
body[data-scroll=false] {
  overflow: hidden;
}
@media (min-width: 768px) {
  body[data-scroll=false] {
    overflow-y: auto;
  }
}

p {
  line-height: 1.7;
  margin: 0;
}

.contenedor {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3, h4, h5 {
  margin: 0 0 2rem 0;
  font-family: "Fraunces", serif;
  font-weight: 900;
}
@media (min-width: 1200px) {
  h1, h2, h3, h4, h5 {
    margin-bottom: 3rem;
  }
}

h1 {
  font-size: 3.85rem;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 0.8rem;
}
@media (min-width: 768px) {
  h1 {
    font-size: 4.4rem;
    letter-spacing: 1.1rem;
  }
}
@media (min-width: 1200px) {
  h1 {
    font-size: 5.6rem;
  }
}

h2 {
  font-size: 3rem;
  color: hsl(212deg, 27%, 19%);
}
@media (min-width: 992px) {
  h2 {
    font-size: 3.8rem;
  }
}
@media (min-width: 1200px) {
  h2 {
    font-size: 4.2rem;
  }
}

h3 {
  font-size: 2.4rem;
  color: hsl(167deg, 40%, 24%);
}
@media (min-width: 992px) {
  h3 {
    font-size: 3rem;
  }
}

h4 {
  margin-bottom: 7.5rem;
  color: hsl(210deg, 4%, 67%);
  font-size: 1.6rem;
  text-transform: uppercase;
  letter-spacing: 0.5rem;
}

h5 {
  color: hsl(212deg, 27%, 19%);
  font-size: 1.8rem;
  margin: 0;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

button {
  background-color: transparent;
  border: 0;
  padding: 0;
}
button:hover {
  cursor: pointer;
}

@keyframes updown {
  0% {
    transform: translateY(0rem);
  }
  50% {
    transform: translateY(2rem);
  }
  100% {
    transform: translateY(0rem);
  }
}